@import url('https://fonts.googleapis.com/css2?family=Sarabun&display=swap');

@font-face {
    font-family: 'Vultura';
    src: local('Vultura'), url('../assets/fonts/VulturaRegular/font.woff2') format('truetype');
    font-weight: 400;
    font-display: fallback;
}

@font-face {
    font-family: 'VulturaOutline';
    src: local('VulturaOutline'), url('../assets/fonts/VulturaOutlineRegular/font.woff2') format('truetype');
    font-weight: 'lighter';
    font-display: fallback;
}
