#work {
    background: var(--black);
    color: var(--white);
    padding: 200px 0 400px;

    .title__h2 {
        text-align: center;
    }

    #work__heading span:nth-child(2) {
        margin-left: -10px;
    }

    .work__header {
        padding-bottom: 80px;
        text-align: center;

        @media only screen and (min-width: 950px) {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }

    .work__bottom {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 100px;
    }

    .work__nextProject {
        button {
            @media only screen and (min-width: 950px) {
                cursor: pointer;
                transition: 0.4s;

                &:hover {
                    color: grey;
                }
            }
        }
    }

    .work__numerals {
        font-size: 40px;
        cursor: default;
        button {
            cursor: pointer;
            font-family: 'VulturaOutline';
            margin-right: 10px;

            &:hover {
                cursor: pointer;
            }
        }

        .work__numerals__selected {
            font-family: 'Vultura';
        }
    }
}
